.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  section.modal-main {
    position:fixed;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    padding: 50px;
    text-align: center;

  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

  .showHideClassName{
    background-color: red;
  }